<template>
  <div class="container">
    <b-form inline class="searchBox mt-3 row offset-7 col-5" autocomplete="off" onsubmit="return false"> 
        <!-- autocomplet / onsubmit     (form       button submit   ) -->
      <b-form-input
        class="form-control d-inline"
        v-model.trim="text"
        placeholder=" "
        @keyup.enter="bookSearch"
      >
      </b-form-input>
      <b-button class="offset-10 col-2 mt-1" @click="bookSearch"></b-button>
    </b-form>
  </div>

</template>

<script>
import { mapActions } from "vuex";

const bookStore = "bookStore";

export default {
  name: 'BookSearch',
  methods: {
    ...mapActions(bookStore, ['searchNaver']),
    bookSearch() {
      console.log(this.text)
      const value = this.text
      return this.searchNaver(value)
    } 
  },
  data: function () {
    return {
      text: ''
    }
  },

}
</script>

