<template>
  <div>
    <div class="Conference"> </div>
    <div class="ChatList"></div>
    <div class="ToolBox"> </div>
    <div class="ChatBox"></div>
    <hr />
    <div>
      <!--     () -->
      <p>  </p>
      <p>
        conference name: {{ conferenceDetail.title }} / conference type: {{ categoryName }}
      </p>
    </div>
    <hr />
    <div>
      <!--   /   -->
      <!--  ()   -->
      <conference-detail-update></conference-detail-update>
      <conference-detail-close></conference-detail-close>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import ConferenceDetailUpdate from './ConferenceDetailUpdate';
import ConferenceDetailClose from './ConferenceDetailClose';

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: "Conference_detail",
  components: {
    ConferenceDetailUpdate,
    ConferenceDetailClose
  },
  data() {
    return {
      userName: "",
      message: "",
      recvList: [],
      //  
      categoryName: "",
      //   
      conferenceId: this.$route.params.contentId,
    };
  },
  created() {
    // App.vue    .
    ///////////////////////////////////////////////

    //    
    // console.log('***')
    this.getConferenceInfo(this.conferenceId);

    //     
    this.matchCategory();
    
    //    
    this.createHistory(this.historyData());

    //  -   ( participant null 200  & participant  ),     
    // console.log('****************')
    // this.createParticipant(this.participantData());

  },
  watch: {
    conferenceDetail() {
      this.matchCategory();
    }, // conferenceDetail  ()    ...
  },
  computed: {
    ...mapState(conferenceStore, ["conferenceDetail", "conferenceCategory"]),
    ...mapState(userStore, ["userInfo"]),
  },
  methods: {
    ...mapActions(conferenceStore, [
      "getConferenceInfo",
      "createHistory", 
      "createParticipant"
    ]),
    date_time() {
      let today = new Date();

      let year = today.getFullYear(); // 
      let month = today.getMonth() + 1; // 
      let date = today.getDate(); // 
      let hours = today.getHours(); // 
      let minutes = today.getMinutes(); // 
      let seconds = today.getSeconds(); // 

      return (
        year +
        "-" +
        month +
        "-" +
        date +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },
    //     
    matchCategory() {
      // this.category = this.conferenceCategory.filter((conferenceCategory) => {
      //     return conferenceCategory.id === this.conferenceDetail.conference_type
      //   })
      for (const cc of this.conferenceCategory)
        if (cc.id === this.conferenceDetail.conference_type) {
          this.categoryName = cc.name;
        }
    },
    historyData() {
      return {
        conference_id: this.conferenceId,
        user_user_id: this.userInfo.user_id,
        // action: 1, //   / null  /   0
        // inserted_time: this.date_time() //      / null  // date_time() -_-.....
      }
    },
    // participantData() {
    //   return {
    //     conferenceInfoDto: {
    //       id: this.conferenceId,
    //       user_user_id: this.userInfo.user_id,
    //     },
    //     userDto: {
    //       user_id: this.userInfo.user_id
    //     }
    //   }
    // }
  },
};
</script>

<style>
.Conference {
  border: solid;
  float: left;
  width: 70%;
  height: 800px;
}
.ChatList {
  border: solid;
  float: right;
  width: 30%;
  height: 800px;
}
.ToolBox {
  border: solid;
  float: left;
  width: 70%;
  height: 200px;
}
.ChatBox {
  border: solid;
  float: right;
  width: 30%;
  height: 200px;
}
</style>
